.card {
    background: #fffaf0;
    border-radius: 2px;
    height: 150px;
    margin: 1.5rem;
    position: relative;
    width: 225px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
}

.card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background: #fffaf0;
}

.card > .img-container > img {
    width: 60%;
}

.card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
}
.wrapper {
    padding-top: 50px;
    background: #faebd9;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 45px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
}
.header {
    width: 100%;
    font-size: 2rem;
    margin-bottom: 10px;
  }
  
  .title {
    float: left;
    margin-left: 25px;
    font-family: 'Arbutus Slab', serif;
  }
  
  .scores {
    float: right;
    margin-right: 25px;
    font-family: 'Arbutus Slab', serif;
  }

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.card {
  cursor: pointer;
}
